







































import Vue from "vue";
import "./style/index.scss";
import { Component } from "vue-property-decorator";
@Component({
  name: "dialogPage"
})
export default class ExDialog extends Vue {
  dialogVisible1 = false;
  dialogVisible2 = false;
  dialogVisible3 = false;
  outerVisible = false;
  innerVisible = false;
  get userList() {
    const list = [];
    for(let i = 0; i < 100; i++) {
      list.push('xx' + i);
    }
    return list;
  }
}
